import { createContext, useState, useEffect } from "react";
import authService from "../services/authService";

// Create context
export const AuthContext = createContext();

// Context provider
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);

  // Load user on app start
  useEffect(() => {
    const token = localStorage.getItem(
      process.env.NEXT_PUBLIC_JWT_STORAGE_KEY
    );
    if (token) {
      authService
        .getProfile()
        .then((res) => {
          setUser(res.user || res);
          setIsAuthenticated(true);
        })
        .catch(() => {
          localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY);
          setUser(null);
          setIsAuthenticated(false);
        })
        .finally(() => setLoading(false));
    } else {
      setLoading(false);
    }
  }, []);

  // Login
  const login = async (email, password) => {
    try {
      const res = await authService.login(email, password);
      const token = res.token; // Make sure your backend returns { token, user }
      localStorage.setItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY, token);

      // Fetch user info
      const userInfo = await authService.getProfile();
      setUser(userInfo.user || userInfo);
      setIsAuthenticated(true);

      return { success: true };
    } catch (err) {
      console.error("Login error:", err.message || err);
      return { success: false, message: err.message || "Login failed" };
    }
  };

  // Signup
  const signup = async (userData) => {
    try {
      await authService.signup(userData);
      return { success: true };
    } catch (err) {
      console.error("Signup error:", err.message || err);
      return { success: false, message: err.message || "Signup failed" };
    }
  };

  // Logout
  const logout = () => {
    authService.logout();
    setUser(null);
    setIsAuthenticated(false);
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        isAuthenticated,
        login,
        logout,
        signup,
        loading,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
